export default {
	data() {
		return {
			title: '',
			newsContents: ''
		}
	},
	onLoad(params) {
		uni.request({
			url: "https://unidemo.dcloud.net.cn/api/news/36kr/"+params.newsId,
			method:'GET',
			data:{},
			success: res => {
				console.log(res);
				this.title = res.data.title;
				this.newsContents = res.data.content;
			},
			fail: () => {},
			complete: () => {}
		})
	},
	methods: {
		
	}
}
